<script type="text/javascript">
export default {
  data () {
    var checkSize = (rule, value, cb) => {
      // 
      const regSize = /^\d+$/
      if (regSize.test(value)) {
        return cb()
      }
      cb(new Error(''))
    }
    return {
      queryInfo: {
        unit: '',
        fullName: '',
        carrierType: '',
        secret: '',
        useState: '',
        useName: '',
        // 
        pageNum: 1,
        // 
        pageSize: 10
      },
      borrowForm: { datas: [], userName: '' },
      borrowDialogVisible: false,
      transferForm: { datas: [], userName: '' },
      transferDialogVisible: false,
      fileURL: '.xlsx',
      actionUrl: this.$http.defaults.baseURL + 'carrier/main/importCarriers',
      editForm: {},
      total: 0,
      unitData: [],
      addFormRules: {
        size: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkSize, trigger: 'blur' }
        ],
        carrierType: [
          { required: true, message: '', trigger: 'blur' },
        ],
        secret: [
          { required: true, message: '', trigger: 'blur' },
        ],
        useState: [
          { required: true, message: '', trigger: 'blur' },
        ],
        fullName: [
          { required: true, message: '', trigger: 'blur' },
        ],
        unit: [
          { required: true, message: '', trigger: 'blur' },
        ],
        product: [
          { required: true, message: '', trigger: 'blur' },
        ],
        gqState: "",
        ypSerial: "",
        wkState: "",
        ypSize: ""
      },
      borrowFormRules: {
        userName: [{ required: true, message: '', trigger: 'blur' }]
      },
      transferFormRules: {
        userName: [{ required: true, message: '', trigger: 'blur' }]
      },
      pcmesgshow: "display:none;",
      carrierlist: [],
      addDialogVisible: false,
      editDialogVisible: false,
      uploadDialogVisible: false,
      selectdp: '',
      selectDepartmentDV: false,
      addForm: {
        serial: '',
        number: '',
        carrierType: '',
        fullName: '',
        useName: '',
        unit: '',
        department: '',
        secret: '',
        useFor: '',
        useDate: '',
        product: '',
        proSerial: '',
        size: '',
        useState: '',
        proColor: '',
        gqState: '',
        wkState: '',
        systemVersion: '',
        systemDate: '',
        ypSerial: '',
        ypSize: ''
      },
      carriertypeoptions: [{
        value: 'U',
        label: 'U'
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }],
      options: [{
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }],
      usestateoptions: [{
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }],
      unitOptions: [],
      fileList: [],
      myHeaders: { Authorization: window.sessionStorage.getItem('token') }
    }
  },
  created () {
    this.getCarrierList();
    this.getUserUnits();
  },
  methods: {
    async getCarrierList () {
      this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
      const { data: res } = await this.$http.post('carrier/main/findAllCarriers', this.queryInfo)
      if (res.code !== 200) {
        return this.$message.error('');
      }
      this.carrierlist = res.data.datas;
      this.total = res.data.total;
    },
    selectDepartmentClosed () {

    },
    selectDPClosed () {
      this.selectDepartmentDV = false;
    },
    openSelectDp () {
      if (this.addForm.unit == '') {
        return this.$message.error("");
      }
      this.selectdp = 'add';
      this.selectDepartmentDV = true;
      this.getDepartMents();
    },
    openSelectDpedit () {
      if (this.editForm.unit == '') {
        return this.$message.error("");
      }
      this.selectdp = 'edit';
      this.selectDepartmentDV = true;
      this.getDepartMents();
    },
    async getDepartMents () {
      this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
      let seunit = '';
      if (this.selectdp == 'add') {
        seunit = this.addForm.unit;
      } else {
        seunit = this.editForm.unit;
      }
      const { data: res } = await this.$http.get('carrier/unit/findAllDepartments?unit=' + seunit);
      if (res.code == 200) {
        this.unitData = JSON.parse(JSON.stringify(res.data))
      }
    },
    selectDP () {
      let node = this.$refs.selectTree.getCheckedNodes();
      let departs = [];
      node.forEach(item => {
        if (!item.isUnit) {
          departs.push(item);
        }
      });
      if (node.departs > 1) {
        return this.$message.warning("");
      }
      let depart = departs[0].label;
      if (this.selectdp == 'add') {
        this.addForm.department = depart;
      } else {
        this.editForm.department = depart;
      }
      this.selectDepartmentDV = false;
    },
    async getUserUnits () {
      this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
      const { data: res } = await this.$http.get('carrier/unit/findAllUnits')
      if (res.code !== 200) {
        return this.$message.error('');
      }
      this.unitOptions = res.data;
    },
    handleCurrentChange (newPage) {
      this.queryInfo.pageNum = newPage;
      this.getCarrierList();
    },
    addDialogClosed () {
      this.$refs.addFormRef.resetFields();
      this.pcmesgshow = "display:none;";
    },
    borrowDialogClosed () {
      this.$refs.borrowFormRef.resetFields();
    },
    transferDialogClosed () {
      this.$refs.transferFormRef.resetFields();
    },
    resetCarrierList () {
      this.queryInfo.unit = '';
      this.queryInfo.fullName = '';
      this.queryInfo.carrierType = '';
      this.queryInfo.secret = '';
      this.queryInfo.useState = '';
      this.queryInfo.useName = '';
      this.getCarrierList();
    },
    editDialogClosed () {
      this.$refs.editFormRef.resetFields();
      this.pcmesgshow = "display:none;";
    },
    uploadDialogClosed () {
      //   this.$refs.editFormRef.resetFields();
    },
    addCarrier () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        // 
        this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
        const { data: res } = await this.$http.post('carrier/main/createCarrier', this.addForm)
        if (res.code == 500) {
          return this.$message.error(res.msg);
        }
        if (res.code == 200) {
          this.$message.success('');
          // 
          this.addDialogVisible = false;
          // 
          this.getCarrierList();
        }
      })
    },
    elcardStyle () {
      var checkSize = (rule, value, cb) => {
        // 
        const regSize = /^\d+$/
        if (regSize.test(value)) {
          return cb()
        }
        cb(new Error(''))
      }
      if (this.addForm.carrierType && (this.addForm.carrierType == "" || this.addForm.carrierType == "")) {
        this.pcmesgshow = "display:block;";
        this.addFormRules.ypSize = [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkSize, trigger: 'blur' }
        ];
        this.addFormRules.gqState = [
          { required: true, message: '', trigger: 'blur' },
        ];
        this.addFormRules.ypSerial = [
          { required: true, message: '', trigger: 'blur' },
        ];
        this.addFormRules.wkState = [
          { required: true, message: '', trigger: 'blur' },
        ];
      } else {
        this.pcmesgshow = "display:none;";
        this.addFormRules.ypSize = "";
        this.addFormRules.gqState = "";
        this.addFormRules.ypSerial = "";
        this.addFormRules.wkState = "";
      }
    },
    async deleteCarrier () {
      let selections = this.$refs.multipleTable.selection;
      if (selections.length === 0) {
        this.$message.error('')
      } else {
        const confirmResult = await this.$confirm(
          `${selections.length}, ?`,
          '',
          {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }
        ).catch(err => err)

        //  confirm
        //  cancel
        // console.log(confirmResult)
        if (confirmResult !== 'confirm') {
          return this.$message.info('')
        }
        this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
        const { data: res } = await this.$http.post('carrier/main/deleteCarrier', selections);

        if (res.code == 500) {
          return this.$message.error(res.msg);
        }
        this.$message.success('');
        this.getCarrierList();
      }
    },
    editCarrierWd () {
      let selections = this.$refs.multipleTable.selection;
      if (selections.length == 0) {
        this.$message.error('')
      } else if (selections.length > 1) {
        this.$message.error('')
      } else if (selections.length == 1) {
        this.editForm = selections[0];
        this.editDialogVisible = true;
        if (this.editForm.carrierType && (this.editForm.carrierType == "" || this.editForm.carrierType == "")) {
          this.pcmesgshow = "display:block;";
          this.addFormRules.ypSize = [
            { required: true, message: '', trigger: 'blur' },
            { validator: checkSize, trigger: 'blur' }
          ];
          this.addFormRules.gqState = [
            { required: true, message: '', trigger: 'blur' },
          ];
          this.addFormRules.ypSerial = [
            { required: true, message: '', trigger: 'blur' },
          ];
          this.addFormRules.wkState = [
            { required: true, message: '', trigger: 'blur' },
          ];
        } else {
          this.pcmesgshow = "display:none;";
          this.addFormRules.ypSize = "";
          this.addFormRules.gqState = "";
          this.addFormRules.ypSerial = "";
          this.addFormRules.wkState = "";
        }
      }
    },
    editCarrier () {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        // 
        this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
        const { data: res } = await this.$http.put('carrier/main/editCarrier/' + this.editForm.carrierId, this.editForm)
        if (res.code == 500) {
          return this.$message.error(res.msg);
        }
        if (res.code == 200) {
          this.$message.success('');
          // 
          this.editDialogVisible = false;
          // 
          this.getCarrierList();
        }
      })
    },
    async handleCommand (command) {
      let selections = this.$refs.multipleTable.selection;
      if (command == "a") {
        this.uploadDialogVisible = true;
      } else if (command == "b") {
        this.downLoadCarriers(selections);
      } else if (command == "c") {
        this.stopCarriers(selections);
      } else if (command == "d") {
        this.borrowDialogVisible = true;
        this.borrowForm.datas = selections;
      } else if (command == "e") {
        this.transferDialogVisible = true;
        this.transferForm.datas = selections;
      }
    },
    async transfer () {
      if (this.transferForm.datas.length == 0) {
        return this.$message.error('');
      }
      this.$refs.transferFormRef.validate(async valid => {
        if (!valid) return
        this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
        const { data: res } = await this.$http.post('carrier/main/transferCarrier', this.transferForm);
        if (res.code == 500) {
          return this.$message.error(res.msg);
        }
        this.$message.success('');
        this.transferDialogVisible = false;
        this.getCarrierList();
      })
    },
    async borrow () {
      if (this.borrowForm.datas.length == 0) {
        return this.$message.error('');
      }
      this.$refs.borrowFormRef.validate(async valid => {
        if (!valid) return
        this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
        const { data: res } = await this.$http.post('carrier/main/borrowCarrier', this.borrowForm);
        if (res.code == 500) {
          return this.$message.error(res.msg);
        }
        this.$message.success('');
        this.borrowDialogVisible = false;
        this.getCarrierList();
      })
    },
    async stopCarriers (selections) {
      if (selections.length == 0) {
        this.$message.error('')
      } else {
        const confirmResult = await this.$confirm(
          `, ?`,
          '',
          {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }
        ).catch(err => err)

        //  confirm
        //  cancel
        // console.log(confirmResult)
        if (confirmResult !== 'confirm') {
          return this.$message.info('')
        } else {
          this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
          const { data: res } = await this.$http.post('carrier/main/stopCarrier', selections);
          if (res.code == 500) {
            return this.$message.error(res.msg);
          }
          this.$message.success('');
          this.getCarrierList();
        }
      }
    },
    async downLoadCarriers (selections) {
      this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
      this.$http({
        method: "post",
        url: "carrier/main/downLoadCarriers",
        data: selections,
        responseType: "blob"
      })
        .then(res => {
          // console.log(decodeURI(res.headers['filename']));
          const link = document.createElement("a");
          let blob = new Blob([res.data], { type: "application/vnd.ms-excel" });
          link.style.display = "none";
          link.href = URL.createObjectURL(blob);
          link.setAttribute("download", decodeURI(res.headers['filename']));
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
        })
        .catch(error => {
          this.$Notice.error({
            title: "",
            desc: ""
          });
          console.log(error);
        });
    },
    dateFormat (row, column, cellValue, index) {
      const daterc = row[column.property]
      if (daterc != null) {
        const dateMat = new Date(daterc);
        const year = dateMat.getFullYear();
        const month = dateMat.getMonth() + 1;
        const day = dateMat.getDate();
        const hh = dateMat.getHours();
        const mm = dateMat.getMinutes();
        const ss = dateMat.getSeconds();
        const timeFormat = year + "-" + month + "-" + day + " " + hh + ":" + mm + ":" + ss;
        return timeFormat;
      }
    },
    dateFormater (row, column, cellValue, index) {
      const daterc = row[column.property]
      if (daterc != null) {
        const dateMat = new Date(daterc);
        const year = dateMat.getFullYear();
        const month = dateMat.getMonth() + 1;
        const day = dateMat.getDate();
        const timeFormat = year + "-" + month + "-" + day;
        return timeFormat;
      }
    },
    sizeFormater (row, column, cellValue, index) {
      if (cellValue) {
        return cellValue + "G";
      }
      return "";
    },
    submitUpload (file, fileList) {
      this.$refs.upload.submit();
      this.$message.success("");
      this.uploadDialogVisible = false;
      location.reload();
    },
    beforeUpload (file) {
      if (file.size / 1024 / 1024 > 5) {
        this.$message.warning('5M')
        return false
      }
      var ext = file.name.substring(file.name.lastIndexOf('.') + 1)
      const extension = ext === 'xlsx';
      if (!extension) {
        this.$message.warning('xlsx')
        return false
      }
    },
    handleChange (file, fileList) {
      this.fileList = fileList;
    }
  }
}
</script>
