<template>
  <div>
    <el-card>
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-card>
    <el-card>
      <el-carousel :interval="4000" type="card" height="300px">
        <el-carousel-item v-for="item in 4" :key="item">
          <div :id="myChart(item)" :style="{width: '600px', height: '300px'}" class="myChart"></div>
        </el-carousel-item>
      </el-carousel>
    </el-card>
    <el-card>
      <el-row>
        <el-col :span="24">
          <el-form :inline="true" :model="queryInfo" class="demo-form-inline" size="mini">
            <el-form-item label="" style="margin-left: 30px;">
              <el-input v-model="queryInfo.fullName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="queryInfo.carrierType" placeholder="">
                <el-option label="U" value="U"></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="queryInfo.secret" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="queryInfo.useState" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="getCarrierList" plain icon="el-icon-search"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="resetCarrierList" plain icon="el-icon-refresh-left"></el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-table :data="carrierlist" :header-cell-style="{background:'#d3e0def5',color:'#303133'}" style="width: 100%;height: 100%" border :highlight-current-row=true :row-style="{height:'50px'}" :cell-style="{padding:'0'}">
            <el-table-column prop="carrierId" label="ID" v-if="false" header-align="center" align="center"></el-table-column>
            <el-table-column prop="serial" label="" fixed width="50" header-align="center" align="center"></el-table-column>
            <el-table-column prop="number" label="" fixed width="150" header-align="center" align="center">
              <template slot-scope='scope'>
                <router-link v-bind:to="{path:'/home/detail',query:{carrierId:scope.row.carrierId,page:'welcome'}}">{{scope.row.number}}</router-link>
              </template>
            </el-table-column>
            <el-table-column prop="carrierType" label="" fixed width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="fullName" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="useName" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="unit" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="department" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="secret" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="useFor" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="useDate" label="" width="150" :formatter="dateFormater" header-align="center" align="center"></el-table-column>
            <el-table-column prop="product" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="proSerial" label="" width="250" header-align="center" align="center"></el-table-column>
            <el-table-column prop="size" label="" width="150" :formatter="sizeFormater" header-align="center" align="center"></el-table-column>
            <el-table-column prop="useState" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="proColor" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="gqState" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="wkState" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="systemVersion" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="systemDate" label="" width="150" :formatter="dateFormat" header-align="center" align="center"></el-table-column>
            <el-table-column prop="ypSerial" label="" width="250" header-align="center" align="center"></el-table-column>
            <el-table-column prop="ypSize" label="" width="150" :formatter="sizeFormater" header-align="center" align="center"></el-table-column>
          </el-table>
        </el-col>
      </el-row>
      <el-row>
        <el-pagination @current-change="handleCurrentChange" background :page-size="queryInfo.pageSize" layout="total, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </el-row>
    </el-card>
  </div>
</template>
<script type="text/javascript">
export default {
  data () {
    return {
      queryInfo: {
        unit: '',
        fullName: '',
        carrierType: '',
        secret: '',
        useState: '',
        useName: '',
        // 
        pageNum: 1,
        // 
        pageSize: 10
      },
      total: 0,
      carrierlist: [],
      unitOptions: [],
      addDialogVisible: false
    }
  },
  mounted () {
    this.drawLine();
  },
  created () {
    this.getCarrierList();
    this.getUserUnits();
  },
  methods: {
    onSubmit () {
      console.log('submit!');
    },
    handleCurrentChange (newPage) {
      this.queryInfo.pageNum = newPage
      this.getUserList()
    },
    async getCarrierList () {
      this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
      const { data: res } = await this.$http.post('carrier/overview/findAllCarriers', this.queryInfo)
      if (res.code !== 200) {
        return this.$message.error('');
      }
      this.carrierlist = res.data.datas;
      this.total = res.data.total;
    },
    resetCarrierList () {
      this.queryInfo.unit = '';
      this.queryInfo.fullName = '';
      this.queryInfo.carrierType = '';
      this.queryInfo.secret = '';
      this.queryInfo.useState = '';
      this.queryInfo.useName = '';
      this.getCarrierList();
    },
    async getUserUnits () {
      this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
      const { data: res } = await this.$http.get('carrier/unit/findAllUnits')
      if (res.code !== 200) {
        return this.$message.error('');
      }
      this.unitOptions = res.data;
    },
    dateFormat (row, column, cellValue, index) {
      const daterc = row[column.property]
      if (daterc != null) {
        const dateMat = new Date(daterc);
        const year = dateMat.getFullYear();
        const month = dateMat.getMonth() + 1;
        const day = dateMat.getDate();
        const hh = dateMat.getHours();
        const mm = dateMat.getMinutes();
        const ss = dateMat.getSeconds();
        const timeFormat = year + "-" + month + "-" + day + " " + hh + ":" + mm + ":" + ss;
        return timeFormat;
      }
    },
    dateFormater (row, column, cellValue, index) {
      const daterc = row[column.property]
      if (daterc != null) {
        const dateMat = new Date(daterc);
        const year = dateMat.getFullYear();
        const month = dateMat.getMonth() + 1;
        const day = dateMat.getDate();
        const timeFormat = year + "-" + month + "-" + day;
        return timeFormat;
      }
    },
    sizeFormater (row, column, cellValue, index) {
      if (cellValue) {
        return cellValue + "G";
      }
      return "";
    },
    drawLine () {
      // 
      let myChart1 = this.$echarts.init(document.getElementById('myChart1'))
      // 
      myChart1.setOption({
        title: {
          //   text: '',
          //   textStyle: {
          //     color: '#f6f2f2'
          //   },
          //   padding: [15, 0, 0, 250]
        },
        tooltip: {},
        xAxis: {
          data: ['U', '', '', '', '', '', '']
        },
        yAxis: {},
        series: [{
          name: '',
          type: 'bar',
          barWidth: 20,
          data: [500, 280, 386, 190, 107, 207, 452],
          itemStyle: {
            normal: {
              color: '#2f89cf',
              opacity: 1,
              barBorderRadius: 5
            }
          }
        }]
      });

      //
      let myChart2 = this.$echarts.init(document.getElementById('myChart2'))
      // 
      myChart2.setOption({
        title: {
          //   text: '',
          //   textStyle: {
          //     color: '#f6f2f2'
          //   },
          //   padding: [15, 0, 0, 250]
        },
        tooltip: {},
        xAxis: {
          data: ['1', '2', '3', '4', '5']
        },
        yAxis: {},
        series: [{
          name: '',
          type: 'bar',
          barWidth: 20,
          data: [500, 280, 386, 190, 107],
          itemStyle: {
            normal: {
              color: '#2f89cf',
              opacity: 1,
              barBorderRadius: 5
            }
          }
        }]
      });

      //
      let myChart3 = this.$echarts.init(document.getElementById('myChart3'))
      // 
      myChart3.setOption({

        title: {
          //   text: '',
          //   textStyle: {
          //     color: '#f6f2f2'
          //   },
          //   left: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          left: 'center',
          top: 'bottom',
          data: ['', '', '', '', '']
        },
        series: [{
          type: 'pie',
          radius: '55%',
          center: ['40%', '50%'],
          data: [
            { value: 10, name: '' },
            { value: 5, name: '' },
            { value: 15, name: '' },
            { value: 25, name: '' },
            { value: 20, name: '' }
          ],
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }
        }]
      });

      //
      let myChart4 = this.$echarts.init(document.getElementById('myChart4'))
      // 
      myChart4.setOption({

        title: {
          //   text: '',
          //   textStyle: {
          //     color: '#f6f2f2'
          //   },
          //   left: 'bottom'
        },
        legend: {},
        tooltip: {},
        dataset: {
          source: [
            ['', 'U', '', '', '', '', '', ''],
            ['1', 12, 20, 25, 20, 18, 35, 16],
            ['2', 22, 10, 23, 10, 10, 15, 26],
            ['3', 32, 40, 15, 25, 28, 20, 36]
          ]
        },
        xAxis: { type: 'category' },
        yAxis: {},
        series: [
          { type: 'bar' },
          { type: 'bar' },
          { type: 'bar' },
          { type: 'bar' },
          { type: 'bar' },
          { type: 'bar' },
          { type: 'bar' }
        ]
      });
    },
    myChart (item) {
      return "myChart" + item;
    }
  }
}
</script>
<style lang="less" scoped>
.myChart {
  margin: 0 auto;
}

.el-carousel__item:nth-child(2n) {
  background-color: #bce38c;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #79ebc9;
}
.el-input--mini {
  width: 150px;
}
.el-select--mini {
  width: 180px;
}
</style>