<template>
  <div>
    <el-card>
      <el-page-header @back="goBack" content="">
      </el-page-header>
    </el-card>
    <el-card>
      <el-divider content-position="left">
        <el-tag type="success">{{carrierDTO.number}}</el-tag>
      </el-divider>
      <el-form :model="carrierDTO" label-width="100px" label-position="right" size="mini">
        <el-row :gutter="24">
          <el-col :span="16">
            <el-card shadow="never">
              <el-row :gutter="24">
                <el-col :span="8">
                  <el-form-item label="">
                    <span>{{carrierDTO.number}}</span>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label="">
                    <span>{{carrierDTO.useState}}</span>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label="">
                    <span>{{carrierDTO.carrierType}}</span>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row :gutter="24">
                <el-col :span="8">
                  <el-form-item label="">
                    <span>{{carrierDTO.secret}}</span>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label="">
                    <span>{{carrierDTO.unit}}</span>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label="">
                    <span>{{carrierDTO.department}}</span>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row :gutter="24">
                <el-col :span="8">
                  <el-form-item label="">
                    <span>{{carrierDTO.useFor}}</span>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label="">
                    <span>{{spanDateFormater(carrierDTO.useDate)}}</span>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label="">
                    <span>{{carrierDTO.product}}</span>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row :gutter="24">
                <el-col :span="8">
                  <el-form-item label="">
                    <span>{{carrierDTO.proSerial}}</span>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label="">
                    <span>{{carrierDTO.size}}G</span>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label="">
                    <span>{{carrierDTO.proColor}}</span>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row :gutter="24">
                <el-col :span="8">
                  <el-form-item label="" prop="fullName">
                    <span>{{carrierDTO.fullName}}</span>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-card>
          </el-col>
          <el-col :span="8">
            <el-card shadow="never" :style="pcmesgshow">
              <el-row :gutter="24">
                <el-col :span="12">
                  <el-form-item label="">
                    <span>{{carrierDTO.gqState}}</span>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="">
                    <span>{{carrierDTO.wkState}}</span>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row :gutter="24">
                <el-col :span="12">
                  <el-form-item label="">
                    <span>{{carrierDTO.systemVersion}}</span>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="">
                    <span>{{spanDateFormater(carrierDTO.systemDate)}}</span>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row :gutter="24">
                <el-col :span="12">
                  <el-form-item label="">
                    <span>{{carrierDTO.ypSerial}}</span>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="">
                    <span>{{carrierDTO.ypSize}}G</span>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-card>
          </el-col>
        </el-row>
      </el-form>
      <el-divider content-position="left">
        <el-tag type="success">{{carrierDTO.number}}</el-tag>
      </el-divider>
      <el-row :gutter="24">
        <el-col :span="24">
          <el-table :data="carrierlist" :header-cell-style="{background:'#d3e0def5',color:'#303133'}" style="width: 100%;height: 100%" border :highlight-current-row=true :row-style="{height:'50px'}" :cell-style="{padding:'0'}">
            <el-table-column prop="carrierId" label="ID" v-if="false" header-align="center" align="center"></el-table-column>
            <el-table-column prop="serial" label="" fixed width="50" header-align="center" align="center"></el-table-column>
            <el-table-column prop="updateUserName" label="" fixed width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="updateTime" label="" fixed width="160" :formatter="dateFormat" header-align="center" align="center"></el-table-column>
            <el-table-column prop="number" label="" fixed width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="carrierType" label="" fixed width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="fullName" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="useName" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="unit" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="department" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="secret" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="useFor" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="useDate" label="" width="160" :formatter="dateFormater" header-align="center" align="center"></el-table-column>
            <el-table-column prop="product" label="" width="250" header-align="center" align="center"></el-table-column>
            <el-table-column prop="proSerial" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="size" label="" width="150" :formatter="sizeFormater" header-align="center" align="center"></el-table-column>
            <el-table-column prop="useState" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="proColor" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="gqState" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="wkState" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="systemVersion" label="" width="150" header-align="center" align="center"></el-table-column>
            <el-table-column prop="systemDate" label="" width="160" :formatter="dateFormat" header-align="center" align="center"></el-table-column>
            <el-table-column prop="ypSerial" label="" width="250" header-align="center" align="center"></el-table-column>
            <el-table-column prop="ypSize" label="" width="150" :formatter="sizeFormater" header-align="center" align="center"></el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </el-card>

  </div>
</template>

<script type="text/javascript">
export default {
  data () {
    return {
      carrierId: '',
      pageType: '',
      carrierDTO: {},
      pcmesgshow: "display:none;",
      carrierlist: []
    }
  },
  created () {
    this.carrierId = this.$route.query.carrierId;
    this.pageType = this.$route.query.page;
    this.getCarrierDetail();
    this.getCarrierHistorys();
  },
  methods: {
    async getCarrierDetail () {
      this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
      const { data: res } = await this.$http.get('carrier/main/carrierDetail/' + this.carrierId);
      if (res.code !== 200) {
        return this.$message.error(res.msg);
      }
      if (res.data.carrierType && res.data.carrierType == "") {
        this.pcmesgshow = "display:block;";
      }
      this.carrierDTO = res.data;

    },
    async getCarrierHistorys () {
      this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
      const { data: res } = await this.$http.get('carrier/main/findHistoryCarriers/' + this.carrierId);
      if (res.code !== 200) {
        return this.$message.error(res.msg);
      }
      this.carrierlist = res.data.datas;

    },
    goBack () {
      if (this.pageType == "carrier") {
        this.$router.push('/home/carrier');
      } else if (this.pageType == "welcome") {
        this.$router.push('/home/welcome');
      }
    },
    dateFormat (row, column, cellValue, index) {
      const daterc = row[column.property]
      if (daterc != null) {
        const dateMat = new Date(daterc);
        const year = dateMat.getFullYear();
        const month = dateMat.getMonth() + 1;
        const day = dateMat.getDate();
        const hh = dateMat.getHours();
        const mm = dateMat.getMinutes();
        const ss = dateMat.getSeconds();
        const timeFormat = year + "-" + month + "-" + day + " " + hh + ":" + mm + ":" + ss;
        return timeFormat;
      }
    },
    dateFormater (row, column, cellValue, index) {
      const daterc = row[column.property]
      if (daterc != null) {
        const dateMat = new Date(daterc);
        const year = dateMat.getFullYear();
        const month = dateMat.getMonth() + 1;
        const day = dateMat.getDate();
        const timeFormat = year + "-" + month + "-" + day;
        return timeFormat;
      }
    },
    spanDateFormater (daterc) {
      if (daterc != null) {
        const dateMat = new Date(daterc);
        const year = dateMat.getFullYear();
        const month = dateMat.getMonth() + 1;
        const day = dateMat.getDate();
        const hh = dateMat.getHours();
        const mm = dateMat.getMinutes();
        const ss = dateMat.getSeconds();
        const timeFormat = year + "-" + month + "-" + day + " " + hh + ":" + mm + ":" + ss;
        return timeFormat;
      }
    },
    sizeFormater (row, column, cellValue, index) {
      if (cellValue) {
        return cellValue + "G";
      }
      return "";
    }
  }
}
</script>

<style lang="less" scoped>
</style>
<style lang="less">
.el-form-item__label {
  color: #3c6aab !important;
}
</style>

