<template>
  <div>
    <el-card>
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-card>
    <el-card style="height:100%" shadow="never">
      <el-row :gutter="24">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.keyWord" clearable @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4" :push="13">
          <el-button type="primary" @click="addDialogVisible = true" size='medium' icon="el-icon-circle-plus" :disabled="limit.createUser"></el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-table :data="userlist" :header-cell-style="{background:'#d3e0def5',color:'#303133'}" style="width: 100%;height: 100%" border :highlight-current-row=true :row-style="{height:'50px'}" :cell-style="{padding:'0'}">
          <el-table-column prop="serial" label="" min-width="5%;" header-align="center" align="center"></el-table-column>
          <el-table-column prop="userId" label="ID" v-if="false" header-align="center" align="center"></el-table-column>
          <el-table-column prop="fullName" label="" min-width="10%" header-align="center" align="center"></el-table-column>
          <el-table-column prop="userName" label="" min-width="10%" header-align="center" align="center"></el-table-column>
          <el-table-column prop="secret" label="" min-width="10%" header-align="center" align="center"></el-table-column>
          <el-table-column prop="unit" label="" min-width="12.5%" header-align="center" align="center"></el-table-column>
          <el-table-column prop="department" label="" min-width="12.5%" header-align="center" align="center"></el-table-column>
          <el-table-column prop="telephone" label="" min-width="10%" header-align="center" align="center"></el-table-column>
          <el-table-column prop="mobilePhone" label="" min-width="10%" header-align="center" align="center"></el-table-column>
          <el-table-column prop="role" label="" min-width="10%" header-align="center" align="center"></el-table-column>
          <el-table-column label="" min-width="10%" header-align="center" align="center">
            <template slot-scope="scope">
              <el-button @click="editUser(scope.row.userId)" type="text" size="medium"></el-button>
              <el-button @click="deleteUser(scope.row.userId)" type="text" size="medium" :disabled="limit.deleteUser"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-row>
      <el-row>
        <el-pagination @current-change="handleCurrentChange" background :page-size="queryInfo.pageSize" layout="total, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </el-row>
      <!--  -->
      <el-dialog title="" :visible.sync="editDialogVisible" width="40%" @close="editDialogClosed" center>
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px" label-position="right" size="small">
          <el-card shadow="never">
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="fullName">
                  <el-input v-model="editForm.fullName" :disabled="limit.updateUser"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="userName">
                  <el-input v-model="editForm.userName" :disabled="limit.updateUser"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="passWord">
                  <el-input v-model="editForm.passWord" type="password" :show-password="false"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="secret">
                  <el-select v-model="editForm.secret" placeholder="" :disabled="limit.updateUser">
                    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="unit">
                  <el-select v-model="editForm.unit" placeholder="" :disabled="limit.updateUser">
                    <el-option v-for="item in unitOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="department">
                  <el-input v-model="editForm.department" :disabled="limit.updateUser">
                    <el-button slot="append" icon="el-icon-s-home" @click="openSelectDpedit" :disabled="limit.updateUser"></el-button>
                  </el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="telephone">
                  <el-input v-model="editForm.telephone"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="mobilePhone">
                  <el-input v-model="editForm.mobilePhone"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="role">
                  <el-select v-model="editForm.role" placeholder="" :disabled="limit.updateUser">
                    <el-option v-for="item in roleOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
          </el-card>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editUserInfo"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="addDialogVisible" width="40%" @close="addDialogClosed" center>
        <!--  -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px" label-position="right" size="small">
          <el-card shadow="never">
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="fullName">
                  <el-input v-model="addForm.fullName"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="userName">
                  <el-input v-model="addForm.userName"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="passWord">
                  <el-input v-model="addForm.passWord" type="password" :show-password="true" autoComplete="new-password"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="secret">
                  <el-select v-model="addForm.secret" placeholder="">
                    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="unit">
                  <el-select v-model="addForm.unit" placeholder="">
                    <el-option v-for="item in unitOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="department">
                  <el-input v-model="addForm.department">
                    <el-button slot="append" icon="el-icon-s-home" @click="openSelectDp"></el-button>
                  </el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="telephone">
                  <el-input v-model="addForm.telephone"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="mobilePhone">
                  <el-input v-model="addForm.mobilePhone"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="18" :offset="3">
                <el-form-item label="" prop="role">
                  <el-select v-model="addForm.role" placeholder="">
                    <el-option v-for="item in roleOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
          </el-card>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="selectDepartmentDV" width="40%" @close="selectDepartmentClosed" center>
        <!--  -->
        <el-card shadow="never">
          <el-tree :data="unitData" default-expand-all node-key="id" show-checkbox :check-on-click-node="true" ref="selectTree" :highlight-current="true">
            <span class="custom-tree-node" slot-scope="{ node}">
              <span>{{ node.label }}</span>
            </span>
          </el-tree>
        </el-card>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="selectDPClosed"> </el-button>
          <el-button type="primary" @click="selectDP"> </el-button>
        </span>
      </el-dialog>
    </el-card>

  </div>
</template>

<script type="text/javascript">
export default {
  data () {
    var checkPassword = (rule, value, cb) => {
      const regPassword = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,30}$/

      if (regPassword.test(value)) {
        return cb()
      }

      cb(new Error('8'))
    }

    // 
    var checkMobile = (rule, value, cb) => {
      // 
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/

      if (regMobile.test(value)) {
        return cb()
      }

      cb(new Error(''))
    }

    return {
      // 
      queryInfo: {
        keyWord: '',
        // 
        pageNum: 1,
        // 
        pageSize: 8
      },
      unitOptions: [],
      roleOptions: [{
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }],
      options: [{
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }],
      limit: { createTopUnit: false, createUnit: false, deleteUnit: false, createUser: false, deleteUser: false, updateUser: false, deleteTopUnit: false },
      userlist: [],
      total: 0,
      // 
      addDialogVisible: false,
      // 
      addForm: {
        fullName: '',
        userName: '',
        passWord: '',
        secret: '',
        unit: '',
        department: '',
        telephone: '',
        mobilePhone: '',
        role: ''
      },
      // 
      addFormRules: {
        fullName: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 16,
            message: '3~16',
            trigger: 'blur'
          }
        ],
        userName: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 16,
            message: '3~16,',
            trigger: 'blur'
          }
        ],
        passWord: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkPassword, trigger: 'blur' }
        ],
        secret: [
          { required: true, message: '', trigger: 'blur' }
        ],
        unit: [
          { required: true, message: '', trigger: 'blur' }
        ],
        department: [
          { required: true, message: '', trigger: 'blur' }
        ],
        role: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      editFormRules: {
        fullName: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 16,
            message: '3~16',
            trigger: 'blur'
          }
        ],
        userName: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 16,
            message: '3~16,',
            trigger: 'blur'
          }
        ],
        passWord: [
          { validator: checkPassword, trigger: 'blur' }
        ],
        secret: [
          { required: true, message: '', trigger: 'blur' }
        ],
        unit: [
          { required: true, message: '', trigger: 'blur' }
        ],
        department: [
          { required: true, message: '', trigger: 'blur' }
        ],
        role: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      setRoleDialogVisible: false,
      selectDepartmentDV: false,
      // 
      userInfo: {},
      // 
      rolesList: [],
      // Id
      selectedRoleId: '',
      selectdp: '',
      unitData: []
    }
  },
  created () {
    this.getUserList();
    this.getUserUnits();
    this.getUserLimit();
  },
  methods: {
    async getUserList () {
      this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
      const { data: res } = await this.$http.post('carrier/user/findAll', this.queryInfo)
      if (res.code !== 200) {
        return this.$message.error('');
      }
      this.userlist = res.data.datas;
      this.total = res.data.total;
    },
    async getUserLimit () {
      this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
      const { data: res } = await this.$http.get('carrier/user/userLimit')
      if (res.code !== 200) {
        return this.$message.error('');
      }
      this.limit = res.data;
    },
    async getDepartMents () {
      this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
      let seunit = '';
      if (this.selectdp == 'add') {
        seunit = this.addForm.unit;
      } else {
        seunit = this.editForm.unit;
      }
      const { data: res } = await this.$http.get('carrier/unit/findAllDepartments?unit=' + seunit);
      if (res.code == 200) {
        this.unitData = JSON.parse(JSON.stringify(res.data))
      }
    },
    async getUserUnits () {
      this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
      const { data: res } = await this.$http.get('carrier/unit/findAllUnits')
      if (res.code !== 200) {
        return this.$message.error('');
      }
      this.unitOptions = res.data;
    },
    //   
    handleCurrentChange (newPage) {
      this.queryInfo.pageNum = newPage
      this.getUserList();
    },
    // 
    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    selectDepartmentClosed () {

    },
    selectDPClosed () {
      this.selectDepartmentDV = false;
    },
    openSelectDp () {
      if (this.addForm.unit == '') {
        return this.$message.error("");
      }
      this.selectdp = 'add';
      this.selectDepartmentDV = true;
      this.getDepartMents();
    },
    openSelectDpedit () {
      if (this.editForm.unit == '') {
        return this.$message.error("");
      }
      this.selectdp = 'edit';
      this.selectDepartmentDV = true;
      this.getDepartMents();
    },
    selectDP () {
      let node = this.$refs.selectTree.getCheckedNodes();
      let departs = [];
      node.forEach(item => {
        if (!item.isUnit) {
          departs.push(item);
        }
      });
      if (node.departs > 1) {
        return this.$message.warning("");
      }
      let depart = departs[0].label;
      if (this.selectdp == 'add') {
        this.addForm.department = depart;
      } else {
        this.editForm.department = depart;
      }
      this.selectDepartmentDV = false;
    },
    // 
    addUser () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        //  
        this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
        const { data: res } = await this.$http.post('carrier/user/create-user', this.addForm);
        if (res.code == 500) {
          return this.$message.error(res.msg);
        }
        if (res.code == 200) {
          this.$message.success('');
          // 
          this.addDialogVisible = false;
          // 
          this.getUserList();
        }

      })
    },
    // 
    editUser (id) {
      let user = this.userlist.find(item => item.userId == id);
      this.editForm = user;
      this.editDialogVisible = true;
    },
    // 
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editUserInfo () {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return;
        // 
        this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
        const { data: res } = await this.$http.put('carrier/user/updateUser/' + this.editForm.userId, this.editForm);

        if (res.code == 500) {
          return this.$message.error(res.msg);
        }

        // 
        this.editDialogVisible = false;
        // 
        this.getUserList();
        // 
        this.$message.success('');
      })
    },
    // Id
    async deleteUser (id) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)

      //  confirm
      //  cancel
      // console.log(confirmResult)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      let user = this.userlist.find(item => item.userId == id);
      if (user.userName == "Administrator") {
        return this.$message.warning("");
      }
      this.$http.defaults.headers.common["Authorization"] = window.sessionStorage.getItem('token');
      const { data: res } = await this.$http.delete('carrier/user/deleteUser/' + user.userId)

      if (res.code == 500) {
        return this.$message.error(res.msg);
      }

      this.$message.success('');
      this.getUserList();
    }
  }
}
</script>

<style lang="less" scoped>
.el-button--primary {
  height: 30px;
  padding: 0;
  line-height: 30px;
  width: 100px;
}
</style>